/* form css */
.form-group {
    position: relative;
}

input.form-control {
    height: 50px;
}

.form-control {
    font-size: 1em;
    box-shadow: none !important;
    border: 1px solid #31383d;
    boder-radius: 0px;
    background-color: transparent;
    transition: all 0.5s;
    padding: .9em 1em;
    color: #f0f0f0;
}

.form-control:focus {
    background-color: transparent;
    border: 1px solid #31383d;
    color: #f0f0f0;
}

.form-focused label:focus {
    background-color: #252b2f;
    opacity: 1;
    color: #1a7bb7;
    transition: translateY(-25px);
}

.form-group label {
    color: #f0f0f0;
    position: absolute;
    top: 11px;
    left: 7px;
    padding: 0 14px;
    transition: all 150ms cubic-bezier(0.4, 0, 0.2, 1);
    pointer-events: none;
    font-size: 1em;
    opacity: .6;
}
